const img = [
  { id: 1, url: '../images/hero-img.jpg' },
  { id: 2, url: 'https://static.educalingo.com/img/ru/800/priroda.jpg' },
  {
    id: 3,
    url: 'https://hlebopechka.ru/images/site/travel/priroda-kanada-1.jpg',
  },
];

const ul = document.querySelector('.pagination-slider-list');
const btnPag = document.querySelectorAll('.pagination-slider-list button');
const heroBackground = document.querySelector('.hero-wrapper-box');
ul.addEventListener('click', handle);

function handle(el) {
  if (el.target.nodeName !== 'BUTTON') {
    return;
  }

  btnPag.forEach(button => {
    button.classList.remove('active-pagination');
  });

  img.forEach(item => {
    if (+el.target.id === +item.id) {
      console.log(el.target);
      el.target.classList.add('active-pagination');
      heroBackground.style.backgroundImage = `url(${item.url})`;
    }
  });
}
